.nav-container {
  display: flex;
  justify-content: space-between;
  background: steelblue;
  height: 30px;
  align-items: center;
  padding: 20px;

  span {
    font-size: 25px;
    text-shadow: 2px 2px 3px black;
  }

  nav {
    a {
      padding: 10px;
      text-decoration: none;
      color: white;
      font-size: 25px;
      text-shadow: 2px 2px 3px black;
      transition: 1s;

      &:hover {
        font-size: 30px;
      }
    }
  }

}


